import React, {Component} from 'react';
import Post from './Post/Post';
import s from './MyPosts.module.css';
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, requiredField} from "../../../utils/validators/validators";
import {Textarea} from "../../common/FormsControls/FormsControls";

const maxlength10 = maxLengthCreator(10);
const MyPosts = React.memo(props => {

        let postsElements = props.posts.map(post => <Post message={post.message}
                                                          key={post.id} like={post.likesCount}/>);

        let addNewPost = (values) => {
            props.addPost(values.newMessagePost);

        };

        return (
            <div className={s.postsBlock}>
                <h3>My posts</h3>
                <div>
                    <AddPostFormRedux onSubmit={addNewPost}/>
                </div>
                <div className={s.posts}>
                    {postsElements}
                </div>
            </div>
        );
});


const AddPostForm = (props) => {
        return (
            <form onSubmit={props.handleSubmit}>
                <div>
                    <Field component={Textarea} placeholder={"Enter your message"}
                           name={'newMessagePost'} validate={[requiredField, maxlength10]}/>
                </div>
                <div>
                    <button>Add post</button>
                </div>
            </form>
        );
};

const AddPostFormRedux = reduxForm({
    // a unique name for the form
    form: 'Posts'
})(AddPostForm);

export default MyPosts;