import React from 'react';
import s from './ProfileInfo.module.css';
import Preloader from "../../common/Preloader/preloader";
import userPhoto from "../../../assets/images/defaultAvatar.jpg";
import ProfileStatus from "./ProfileStatus/ProfileStatus";
import ProfileStatusWithHooks from "./ProfileStatus/ProfileStatusWithHooks";



const ProfileInfo = (props) => {
    if (!props.profile) {
        return <Preloader/>
    } else  {
        return (
            <div>
                <div>
                    <img className={s.pictureSize}
                         src="https://www.amd.com/system/files/2019-06/237107-Radeon_Image_Sharpening_ON_Strange_Brigade_1260x400_1.jpg"/>
                </div>

                <div className={s.descriptionBlock}>

                    <img src={props.profile.photos.large != null ? props.profile.photos.large: userPhoto}/>

                    <ProfileStatusWithHooks status={props.status} updateStatus={props.updateStatus}/>
                    <div>NickName: {props.profile.fullName}</div>
                </div>

            </div>
        );
    }
};


export default ProfileInfo;