import React from 'react';
import s from './Post.module.css';

const Post = (props) => {
    return (
        <div className={s.item}>
            <img src="https://i.pinimg.com/originals/10/02/6c/10026ceb25e791fa3601f46ab627d416.jpg" />
            {props.message}
            <div>
                <span>like</span>{props.like}
            </div>
        </div>

    );
}

export default Post;